*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
  outline: none;
  backface-visibility: hidden;
}

html {
  scroll-behavior: smooth;
}

@font-face {
  font-family: 'Exo2';
  src: url(./assets/fonts/Exo2-Regular.ttf);
}
@font-face {
  font-family: 'Exo2-Medium';
  src: url(./assets/fonts/Exo2-Medium.ttf);
}
@font-face {
  font-family: 'Exo2-Bold';
  src: url(./assets/fonts/Exo2-Bold.ttf);
}

:root {
  --spacing: 20px;
  --font: 'Exo2', sans-serif;
  --font-medium: 'Exo2-Medium', sans-serif;
  --bradius: 25px;
  --active: #F57512;
  --main-bcolor: #1246F5;
  --main-tcolor: #fff;
  --danger-bcolor: #C25A6C;
  --danger-tcolor: #fff;
  --gray-bcolor: #F1F1F1;
  --gray-tcolor: #000;
  --link-color: #1246F5;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
  font-family: var(--font-medium);
  transition: .3s;
  font-size: 14px;
  text-transform: none;
  color: var(--link-color);
  letter-spacing: 0.02em;
}

img {
  width: inherit;
  height: inherit;
  object-fit: cover;
  max-width: 100%;
}

button {
  cursor: pointer;
  font-size: 16px;
  border-radius: var(--bradius);
  letter-spacing: 0.03em;
  overflow: hidden;
  position: relative;
  z-index: 1;
  color: #999999;
  border: 2px solid #999999;
  background-color: #F9F9F9;
}

button, .link-button, input {
  border: none;
  font-family: var(--font-medium);
  transition: .3s;
  text-overflow: ellipsis;
  overflow: hidden;
}

.gray {
  border-color: #999999;
  background-color: var(--gray-bcolor);
  color: #000;
}

.main {
  border-color: #1246F5;
  background-color: var(--main-bcolor);
  color: var(--main-tcolor);
  border-color: var(--main-bcolor);
}

.success {
  border-color: #4EAE53;
  background-color: #F6FFF7;
  color: #4EAE53;
}

.danger {
  border-color: #C25A6C;
  background-color: #F9EBEE;
  color: #C25A6C;
}

.fade-in {
  animation: fade-in .3s forwards ease-in-out;
}

input, textarea {
  font-family: var(--font);
  padding: 15px 20px;
  font-size: 15px;
  border-radius: 3px var(--bradius) var(--bradius) 3px;
  letter-spacing: 0.02em;
  color: #000 !important;
  border: none;
  background-color: #fff !important;
  border-left: 2px solid;
  box-shadow: 0px 0px 9px 1px rgb(18,70,245, .14);
  display: block;
  width: 100%;
}

input[type="checkbox"] {
  appearance: none;
  -webkit-appearance: none;
  min-width: 34px;
  width: 34px;
  height: 22px;
  padding: 0px;
  border-radius: var(--bradius);
  background-color: #fff !important;
  position: relative;
  cursor: pointer;
  border: none;
}
input[type="checkbox"]:after {
  content: '';
  position: absolute;
  top: 5px;
  left: 5px;
  width: 12px;
  height: 12px;
  border-radius: calc(var(--bradius) - 2px);
  transition: .2s;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, .2);
  background-color: #9e9e9e;
}
input[type="checkbox"]:checked:after {
  transform: translateX(100%);
  transition: .2s;
  background-color: var(--main-bcolor);
}

button:hover, a:hover {
  transition: .1s;
  opacity: .8;
}

/* input:hover, input:focus,
textarea:hover, textarea:focus {
  border-color: transparent !important;
} */

input::placeholder,
textarea::placeholder {
  color: #000;
  font-family: var(--font);
  opacity: .6;
}

textarea {
  height: 200px;
  transition: .3s;
  resize: none;
  line-height: 20px;
}

.link-icon.link-icon {
  transition: .3s;
  border-radius: var(--bradius);
  margin: 5px;
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.link-icon i {
  font-size: 20px;
}

.link-icon i,
.link-button i {
  pointer-events: none;
}

.link-button {
  text-align: center;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: .3s;
  padding: 12px 15px;
  border-radius: var(--bradius);
  text-decoration: none;
}

.link-button.link-button i {
  font-size: 20px;
}

.active {
  background-color: var(--active);
}

input:focus, textarea:focus {
  border-color: var(--active);
}

button img {
  pointer-events: none;
}

html {
  min-height: 100vh;
}

body {
  font-family: var(--font);
  position: relative;
  overflow: hidden;
  background-color: var(--gray-bcolor);
  background-color: #dadada;
}

body.freeze {
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-medium);
  font-weight: normal;
}

.wrapper {
  padding: 0px var(--spacing);
  max-width: 1580px;
  margin: 0 auto;
}

.under {
  border: none;
  background-color: transparent;
  text-decoration: underline;
  cursor: pointer;
}

.disable {
  opacity: .5;
  cursor: not-allowed;
  pointer-events: none;
}

.nb.nb.nb {
  border: none;
}

#root {
  overflow-y: scroll;
  height: 100vh;
}

#prompt:not(:empty) {
  position: fixed;
  top: 20px;
  width: calc(100% - 40px);
  margin: 0px 20px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 0;
}


@media (max-width: 500px) {
  button {
    font-size: 13px;
  }
  .link-button img {
    width: 15px;
    height: 15px;
  }
  .link-button {
    font-size: 0.9em;
  }
  :root {
    --spacing: 10px;
  }
}

@keyframes rendered {
  0% {
    opacity: 0;
    transform: scale(.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes deleting {
  0% {
    transform: scale3d(1, 1, 1);
    background-color: #fff;
  }
  15% {
    transform: scale3d(1.03, 0.97, 1);
  }
  30% {
    transform: scale3d(0.97, 1.07, 1);
  }
  45% {
    transform: scale3d(1.07, 0.93, 1);
    background-color: #F9EBEE;
  }
  60% {
    transform: scale3d(0.97, 1.02, 1);
  }
  75% {
    transform: scale3d(1.03, 0.97, 1);
  }
  100% {
    transform: scale3d(1, 1, 1);
    background-color: #fff;
  }
}

@keyframes on-close {
  50% {
    opacity: 0;
  }
  100% {
    transform: scale(0);
    opacity: 0;
    margin-left: 0px;
    margin-right: 0px;
    width: 0px;
    padding: 0;
  }
}

@keyframes scroll-up {
  0% {
    transform: translateX(-110%);
  }
  100% {
    transform: translateX(110%);
  }
}

@keyframes scale-down {
  0% {
    transform: scale(1.07);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes scale-up {
  0% {
    opacity: 0;
    transform: scale(.95);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes prompt {
  0% {
    transform: translateY(-105%);
    opacity: 0;
  }
  7% {
    transform: translateY(0%);
    opacity: 1;
  }
  93% {
    transform: translateY(-0%);
    opacity: 1;
  }
  100% {
    transform: translateY(-105%);
    opacity: 0;
  }
}

@keyframes slide-left {
  0% {
    opacity: 0;
    transform: translateX(10%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}

@keyframes of-shadow {
  0% {
    box-shadow: none;
  }
  100% {
    box-shadow: 0px 0px 10px 3px rgba(0, 0, 0, 0.07);
  }
}

@keyframes deadline {
  0% {
    width: 0%;
  }
  100% {
    width: 100%;
  }
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes item {
  0% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0px);
  }
}

.d {
  animation: close-menu 1s forwards ease-in-out;
}

@keyframes close-menu {
  50% {
    transform: translateX(-100%);
  }
}

